.diploma-and-certificate
    display: flex
    flex-direction: column
    justify-content: center
    margin-top: 43px

.average-block-imgs, .big-block-imgs, .large-block-imgs, .small-block-imgs
    display: flex
    justify-content: center
    margin-bottom: 63px

.block-img
    margin-right: 30px
    &:last-child
        margin-right: 0

.average-block-imgs
    .block-img
        width: 540px
        height: 511px

.big-block-imgs
    .block-img
        width: 730px
        height: 730px

.small-block-imgs
    .block-img
        width: 350px
        height: 360px
.large-block-imgs
    .block-img
        width: 920px
        height: 730px